<template>
  <div class="exchange mt-5">
    <div class="content p-5 p-ml-2 mx-auto">
      <div class="banner"></div>
      <div class="flex-wrap d-flex justify-content-center align-items-center mt-3">
        <div class="py-2 cursor-point px-3 mb-2 text-nowrap rounded border text-orange mx-2" :class="{'btn-checked':type === 'all'}" @click="type = 'all'">ALL</div>
        <div class="py-2 cursor-point px-3 mb-2 text-nowrap rounded border text-orange mx-2" :class="{'btn-checked':type === 'easycard'}" @click="type = 'easycard'">悠遊卡</div>
        <div class="py-2 cursor-point px-3 mb-2 text-nowrap rounded border text-orange mx-2" :class="{'btn-checked':type === 'uupon'}" @click="type = 'uupon'">UUPON</div>
        <div class="py-2 cursor-point px-3 mb-2 text-nowrap rounded border text-orange mx-2" :class="{'btn-checked':type === 'reward'}" @click="type = 'reward'">抽獎</div>
        <div class="py-2 cursor-point px-3 mb-2 text-nowrap rounded border text-orange mx-2" :class="{'btn-checked':type === 'coupon'}" @click="type = 'coupon'">超優卷</div>
      </div>
        <div class="p-2 d-flex flex-wrap">
          <div class="w-half p-5 bg-gray mr-1 mt-2" v-for="(item, index) in exchangeList[type]" :key="index">
            <p>{{ item.title }}</p>
            <p>{{ item.point }}</p>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'exchange',
  data () {
    return {
      type: 'all',
      exchangeList: {
        all: [
          { title: '乖乖', point: 20 },
          { title: '乖乖2', point: 30 },
          { title: '乖乖3', point: 10 },
          { title: '乖乖4', point: 220 },
          { title: '乖乖5', point: 2450 }
        ],
        easycard: [
          { title: 'easycar', point: 20 },
          { title: 'easycar', point: 30 },
          { title: '乖乖3', point: 10 },
          { title: 'easycar', point: 220 },
          { title: '乖乖5', point: 2450 }
        ],
        uupon: [
          { title: 'uupon', point: 20 },
          { title: 'uupon', point: 30 },
          { title: 'uupon', point: 10 },
          { title: '乖乖4', point: 220 },
          { title: '乖乖5', point: 2450 }
        ],
        reward: [
          { title: 'reward', point: 20 },
          { title: 'reward', point: 30 },
          { title: 'reward', point: 10 },
          { title: '乖乖4', point: 220 },
          { title: '乖乖5', point: 2450 }
        ],
        coupon: [
          { title: 'coupon', point: 20 },
          { title: 'coupon2', point: 30 },
          { title: 'coupon3', point: 10 },
          { title: 'coupon4', point: 220 },
          { title: '乖乖5', point: 2450 }
        ]
      }
    }
  },
  watch: {
    type () {

    }
  },
  methods: {
    lastpage () {
      this.$router.back()
    }
  }
}
</script>

<style>
.exchange .content{
  min-height: calc( 100vh - 221px);
}
</style>



